.toSvg{
	width: 100%;
	height: 100%;
	&.svgPrimary{
		polygon,
		path{
			fill: $primary !important;
			stroke: none !important;
		}
	}
	&.svgSecondary{
		polygon,
		path{
			fill: $secondary !important;
			stroke: none !important;
		}
	}
	&.svgWhite{
		polygon,
		path{
			fill:$white !important;
			stroke: none !important;
		}
	}
	&.svgBlack{
		polygon,
		path{
			fill: $black !important;
			stroke: none !important;
		}
	}
}


