.c-btn-outline{ 
	display: inline-block;
	padding: 17px 30px;
	border-radius: 17px; 
	text-align: center; 
	color: #9e9e9e;
	text-transform: uppercase;
	font-size: 17px; 
	line-height: 1em; 
	position: relative; 
	border: 1px solid #909090;
	position: relative;

	&:after{
		content: " ";
		width: 100%;
		height: 100%;
		position: absolute;
		border-radius: 22px; 
		left: 0;
		top: 0;
		z-index: -1;
		transition: all 0.5s ease;
		display: none;	
	}

	&:hover{
	    background: #6e3896;
	    color: #fff;
	    border-color: #6e3896;
	    
		&:after{ 
			transition: all 0.5s ease;
			width: 110%;
			height: 130%;
			left: -5%;
			top: -15%;
			opacity: 0;
			border: 3px solid #909090;
		}
	}

	@media (max-width: 1800px){ 
		font-size: 18px; 
		padding: 15px 30px;
	}
	@media (max-width: 1023px){ 
	    font-size: 16px;
    	padding: 14px 24px;
	}

	&.btn--sm{
		font-size: 14px;
		padding: 15px 30px 14px;
		@media (max-width: 767px){
			font-size: 13px;
			padding: 12px 25px;
		}
	}
}
.c-btn-glow{
	background: #552879; /* Old browsers */
	background: -moz-linear-gradient(-45deg, #552879 0%, #311758 25%, #170a3f 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(-45deg, #552879 0%,#311758 25%,#170a3f 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(135deg, #552879 0%,#311758 25%,#170a3f 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#552879', endColorstr='#170a3f',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
	transition: none;

	&:after{
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		background: linear-gradient(to bottom, #d8aff6 0%,#6d217f 50%,#22063c 100%) center 100% / 100% 150%;
		opacity: 0;
		transition: all .35s ease-in-out;
		position: absolute;
		top: 0;
		left: 0;
		mix-blend-mode: color-dodge;
		z-index: 1;
	}

	span{
		position: relative;
		z-index: 2;
	}

	&:hover{
		&:after{
			opacity: 0.5;
		}
	}
	&:active{
		box-shadow: 2.5px p0x 5px 0px rgba(0, 0, 0, 0.3), inset 0px -4px 9px 1px rgba(226, 190, 255, 0.15);
		background: #521766;
		&:hover{
			&:after{
				display: none;
				transition: none;
			}
		}
	}
	
	@media (max-width: 767px){
		&:after{
			display: none;
		}
		&.btn--m-change-to-green{
			background: #000;
			background: linear-gradient(to right, #529297 0%,#0a676f 30%,#203250 100%) no-repeat center center / 100% 100%;
			box-shadow: none;
			&:active{
				background: #164a5f;
			}
		}
	}

	&.sty--a{
		background: rgba(255,255,255,0);
		color: #000;
		border: 1px solid #000;
		transition: all .35s;
		&:after{
			background: linear-gradient(135deg, #552879 0%,#311758 25%,#170a3f 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
			-webkit-mix-blend-mode: unset;
			mix-blend-mode: unset;
		}
		&:hover{
			color: #fff;
			background: rgba(255,255,255,0);
			border-color: #552879;
			border-color: transparent;

			&:after{
				opacity: 1;
			}
		}
	}
}

