.is--loading{
	position: relative;
	&:before{
		content: "";
		display: block;
		position: absolute;
		z-index: 10;
		top: -10px;
		left: 0;
		width: 100%;
		background: #fff;
		height: 100%;
		opacity: 0.75;
		padding-bottom: 10px;
		box-sizing: content-box;
	}
	.u-loader{
		display: block;
		position: absolute;
		top: 50px;
		z-index: 20;
		left: 50%;
		transform: translateX(-50%);
	}
}
.u-loader,
.u-loader:before,
.u-loader:after {
  background: #6e3996;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.u-loader {
  display: none;
  color: #6e3996;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.u-loader:before,
.u-loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.u-loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.u-loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}


@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

